input:-webkit-autofill::first-line {
  font-size: 20px
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
}

.loginNavbar {
  text-align: right;
  margin-top: 10px;
  padding-right: 20px;
  border-bottom: 2px solid lightgray;
}

.nonMobileForm {
  display: none;
}

.regText {
  color: gray;
  font-size: 30px;
}

.registration {
  text-decoration: none;
}

.registration:hover {
  text-decoration: underline;
  text-decoration-color: gray;
}

.registration:visited {
  color: black;
}

.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 20px 20px 20px;
}

h1 {
  font-size: 400%;
  color: rgb(31, 46, 44);
  font-family: Arial, Helvetica, sans-serif;
}

.mobileInputs {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

input {
  margin-bottom: 10px;
}

button {
  width: 200px;
  height: 50px;
  background-color: white;
  color: gray;
  border: 2px solid gray;
  border-color: lightgray;
  font-size: 100%;
}

input {
  width: 160%;
  box-sizing: border-box;
  border-width: medium;
  border-color: lightgray;
  border-style: solid;
  color: black;
  font-size: 20px;
  padding: 10px;
  margin-right: 10px
}

input::placeholder {
  padding-top: 10px;
  padding-bottom: 10px;
  color: gray;
  font-size: 20px;
}

.instructions {
  max-width: 60%;
  font-family: Arial, Helvetica, sans-serif;
  text-align: left;
  background-color: rgb(2, 127, 181);
  padding: 30px 30px 30px 30px;
  border-radius: 25px;
  font-size: 150%;
}


@media (min-width: 768px) {
  .loginNavbar  {
    display: flex;
    justify-content: space-between;
    font-size: 200%;
    padding-left: 20px;
    margin-top: 10px;
    border-bottom: 2px solid lightgray;
  }

  .form {
    display: flex;
    flex-direction: row;
  }

  .mobileLandingForm {
    display: none;
  }

  .nonMobileForm {
    display: initial;
  }

  .registration {
    text-decoration: none;
    padding-top: 10px;
    margin-right: 20px;
    font-family: Arial, Helvetica, sans-serif;
  }

  .regText {
    display: initial;
    font-size: 30px;
  }

  h1 {
    font-size: 500%;
  }

  .instructions {
    font-size: 200%;
  }

  button {
    width: 100px;
    height: 50px;
    font-size: 80%;
    margin-right: 10px;
  }

}
